import React, { Component } from "react";
import "./Services.css";
import { Container, Row, Col, Card, CardImg} from "react-bootstrap";
import  Technologies from './Technologies/Technologies.js';
// import Migration from './Migration/Migration.js';
import consultancy from '../../images/consultation.svg';
import manager from '../../images/manager.svg';
import meeting from '../../images/meeting.svg';
import networking from '../../images/networking.svg';
import service from '../../images/Services-img.svg';
import itStaffing from '../../images/itStaffingIcon.svg';
import serviceIll from '../../images/service-illutration.svg';
import staff from '../../images/Staffing.svg';
import feather from '../../images/feather_award.svg';
import featherStar from '../../images/feather_star.svg';
import itCons from '../../images/It-con-icon.svg';
import itConIll from '../../images/It-consult-illustration.svg';
import migIcon from '../../images/mig-icon.svg';
import migImg from '../../images/mig-Image.svg';
import migImg1 from '../../images/mig-img1.svg';
import migImg2 from '../../images/mig-img2.svg';
import migImg3 from '../../images/mig-img3.svg';
import migImg4 from '../../images/mig-img4.svg';
import migImg5 from '../../images/mig-img5.svg';

// import { CSSTransition } from 'react-transition-group';


class Services extends Component {
  state={
    showItem:"iTConsulting"
  }
  navigateInServices(item){
    this.setState({
      showItem:item
    })
  }
  render() {
    return (
      <Container fluid className="service-container">
        <div className="service-head-cont">
        <div className="service-sec">
          <img src={service} alt="service image" />
          <h3>Services</h3>
          <p>We provide a variety of services to help you in various sectors. Let us help you reach your goals as a company.</p>
          </div>
          </div>
         <div>
            <Row>
              <Col xs="0" lg="1"></Col>
              <Col xs="6" md="5" lg="2" className="service-item-sec">
                <Card className="service-item" onClick={()=>this.navigateInServices("iTConsulting")}>
                  <Card.Img className="service-img" variant="top" src={consultancy} width="30px" height="130px" />
                  <Card.Body>
                    <Card.Title>IT Consulting and Services</Card.Title>
                  </Card.Body>
                </Card>
              </Col>
              {/* <Col xs="1" lg="0"></Col> */}
              <Col xs="6" md="5" lg="2" className="service-item-sec">
              <Card className="service-item" onClick={()=>this.navigateInServices("iTTech")}>
                  <Card.Img className="service-img" variant="top" src={manager} width="30px" height="130px" />
                  <Card.Body>
                    <Card.Title>Technology Focus</Card.Title>
                  </Card.Body>
                </Card>
              </Col>
              <Col xs="6" md="5" lg="2" className="service-item-sec">
              <Card className="service-item" onClick={()=>this.navigateInServices("iTStaffing")}>
                  <Card.Img className="service-img" variant="top" src={meeting} width="30px" height="130px" />
                  <Card.Body>
                    <Card.Title>IT Staffing and Consulting</Card.Title>
                  </Card.Body>
                </Card>
              </Col>
              <Col xs="6" md="5" lg="2" className="service-item-sec">
              <Card className="service-item" onClick={()=>this.navigateInServices("migration")}>
                  <Card.Img className="service-img" variant="top" src={networking} width="30px" height="130px" />
                  <Card.Body>
                    <Card.Title>Migration</Card.Title>
                  </Card.Body>
                </Card>
              </Col>
              <Col></Col>
            </Row>
          </div>
         {this.state.showItem === "iTStaffing" &&
         <div>
          <div className="itStaffing-Sec">
            <div className="itStaff-head">
            <img src={itStaffing} alt="IT Staffing" />
            <h3>IT Staffing and Consulting</h3>
            <h6>Accomplish More by Finding the Best Fit</h6>
            </div>
            <Row>
              <Col md="2"></Col>
              <Col xs="12" md="4">
              <img src={serviceIll} className="ill-img" alt="service illustration" />
              </Col>
              <Col xs="12" md="5" className="tech-content">
                <p>With major companies evolving continuously in a dynamic IT market, there is an acute demand for a quality talent pool in the industry. Addressing this ever-increasing demand, we provide access to a pool of digitally trained and motivated talent, and help you achieve standardization of key processes to improve efficiency.</p>
                <br></br>
                <p>Bridging the gap between enterprises and seasoned workforce, our agile services allow you to focus on critical business transformation and growth, without having to be in pursuit of finding the right candidate.</p>
                <br></br>
                <p>Whether you’re looking to add to your permanent team or need reliable temporary staff, we do all the background checks to help you find the right people, with the right capabilities.</p>
              </Col>
              <Col></Col>
            </Row>
          </div>
          <br></br><br></br>
          <div className="extra-it-services">
            <h3>Our Services Include</h3>
            <br></br>
            <Row>
              <Col></Col>
              <Col lg="3" xs="12">
                <Card className="extra-service-card">
                  <CardImg src={staff} alt="service item"></CardImg>
                  <Card.Title>Project Based Staffing</Card.Title>
                  <Card.Body>
                  We plug the gap in valuable skills while also reducing costs, to finish a short- or long-term project and meet your deadlines.
                  </Card.Body>
                </Card>
              </Col>
              <Col lg="3" xs="12" className="3">
              <Card  className="extra-service-card">
                  <CardImg src={feather} alt="service item"></CardImg>
                  <Card.Title>Contract to Hire</Card.Title>
                  <Card.Body>
                  Helping you increase workforce flexibility – we are experts at identifying, referring and retaining great talent whether it is on temp-to-perm, temp-to-hire or just on contract basis.
                  </Card.Body>
                </Card>
              </Col>
              <Col lg="3" xs="12" className="3">
              <Card className="extra-service-card">
                  <CardImg src={featherStar} alt="service item"></CardImg>
                  <Card.Title>Long / Short term Staffing</Card.Title>
                  <Card.Body>
                  As a strategic recruitment company, we assist our clients in fulfilling their short-term, long –term, as well as the full-time requirements.
                  </Card.Body>
                </Card>
              </Col>
              <Col></Col>
            </Row>
          </div>
          </div>}
          {this.state.showItem === "iTConsulting" && <div className="itStaffing-Sec">
            <div className="itStaff-head">
            <img src={itCons} alt="IT Staffing" />
            <h3>IT Consulting and Services</h3>
            <h6>Industry Focus (System Integrators)</h6>
            </div>
            <Row>
              <Col md="2"></Col>
              <Col xs="12" md="4">
              <img src={itConIll} className="ill-img" alt="service illustration" />
              </Col>
              <Col xs="12" md="5" className="tech-content">
                <p>Complex technology doesn’t always require complex strategies and solutions. At Golden Future Technologies, we offer integrated solutions for even the most complex systems to become efficient, stable, and simple to operate. From design to implementation and further optimization, our team focuses on the intricacies of your business to deliver comprehensive IT integration consulting and services. This obsessive attention allows us to consistent results in a way that makes sense for your business. In addition to serving across a multitude of industries from Automobile to healthcare, retail, education, manufacturing and more, we also apply our expertise to create a custom solution to achieve maximum efficiency, data flow and digital connectedness.</p>
              </Col>
              <Col></Col>
            </Row>
          </div>}
          {this.state.showItem === "iTTech" && <Technologies />}

          {this.state.showItem === "migration" &&
          <div className="mig-sec">
          <div className="itStaff-head">
            <img src={migIcon} alt="IT Staffing" />
            <h3>Migration Services</h3>
            <h6>Accurate and Transparent Migration Solutions.</h6>
            <br></br>
            <p>Golden Future Technologies offers a dedicated team of specialists committed to delivering exceptional visa services, relocation services, immigration policy advisory, corporate visas and all other kind of visa requirements. Understanding the intrinsic nuances, ongoing challenges and compliance obligations, we ensure consistent, ethical, and personalized guidance throughout our engagement with you. Our innovative and cost-effective solutions coupled with our exceptional technical skills and commitment, help you achieve the best solution for all your concerns.</p>
            </div>
            <br>
            </br><br></br>
            <h3>Our Specialized Services</h3>
            <Row>
              <Col lg="6">
                <Card className="mig-card">
                  <CardImg src={migImg} alt="employee visa"></CardImg>
                  <Card.Body>
                    <Card.Title>Employer Sponsored Visas</Card.Title>
                    <Card.Body>
                    With a comprehensive understanding of migration laws strengthened by our knowledge and expertise, we provide the confidence and assurance of a successful visa Employer Sponsored Visa application.
As ENS, RSMS, 482 and DAMA Visas all have different requirements to be fulfilled, we assist the employer and the visa applicant to meet the varied requirements with our straight forward solutions.
                    </Card.Body>
                  </Card.Body>
                </Card>
              </Col>
              <Col lg="6">
              <Card className="mig-card">
                  <CardImg src={migImg1} alt="General visa"></CardImg>
                  <Card.Body>
                    <Card.Title>General Skilled Visas</Card.Title>
                    <Card.Body>
                    If you’re seeking good-paying Australian jobs and want to enjoy the high standard of living found in Australia’s amazing cities, a General Skilled Visa to Australia can unlock the door to many great possibilities.Whether it is a family sponsored, state sponsored, or regional sponsored, our smooth processing assures consultations, assessment and error free submission of documents under the relevant subclass and eligibility.To assure maximum success rate, we also assist in obtaining the skill assessment for the relevant occupation from the assessing authorities.
                    </Card.Body>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
            <Row>
              <Col lg="6">
                <Card className="mig-card">
                  <CardImg src={migImg2} alt="employee visa"></CardImg>
                  <Card.Body>
                  <Card.Title>Family Visa</Card.Title>
                    <Card.Body>
                    Our full-fledged Family visa services require minimum effort from your side to welcome your family, parents, partner or a visitor with utmost ease, pleasance and convenience.
A team of highly-qualified and seasoned experts, we act completely on your behalf to carry out all eligibility requirements with our specialized domain knowledge.Whether you are applying for a fresh admission or changing your qualification and university, we guide you throughout the application process.
                    </Card.Body>
                  </Card.Body>
                </Card>
              </Col>
              <Col lg="6">
              <Card className="mig-card">
                  <CardImg src={migImg3} alt="General visa"></CardImg>
                  <Card.Body>
                    <Card.Title>Student Visas</Card.Title>
                    <Card.Body>
                    With a mission to helping you securing a student visa, we provide comprehensive advice in every step of the way, as well as assistance to guide you and save you many hours and worries.Understanding the purpose and in line with your enrolment, our consistent effort is to use our experience skills and technological resources to completely nullify anxiety and provide peace of mind.Whether you are applying for a fresh admission or changing your qualification and university, we guide you throughout the application process.
                    </Card.Body>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
            <Row>
              <Col lg="6">
                <Card className="mig-card">
                  <CardImg src={migImg4} alt="employee visa"></CardImg>
                  <Card.Body>
                    <Card.Title>Business and Investment Visa</Card.Title>
                    <Card.Body>
                    Getting an Investment Visa entitles you to live, travel, work or study in that country. Each country has their own set of rules and immigration rules.Whether you are establishing a new business or making investment in a business venture abroad, we do a systematic study of your specific circumstances in tandem with the immigration rules, and offer effective and accurate guidance pertaining to the application.
                    </Card.Body>
                  </Card.Body>
                </Card>
              </Col>
              <Col lg="6">
              <Card className="mig-card">
                  <CardImg src={migImg5} alt="General visa"></CardImg>
                  <Card.Body>
                    <Card.Title>Temporary Visa</Card.Title>
                    <Card.Body>
                    Temporary visas allow people to work, study and visit a country for a set period of time for a specific reason.Depend on your case, your goals, and your intent to visit the particular country, we meticulously prepare your application package, ensuring that it more than meets the requirements.We put in order a complete and well documented application, and help you sail through the complicated and changing rules and regulations.
                    </Card.Body>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
            </div>
        }
      </Container>

    );
  }
}

export default Services;
