import React, { Component } from 'react';
import {Row,Col,Container,Card} from 'react-bootstrap';
import img1 from '../../images/group7.svg';
import img8 from "../../images/cl.jpg";
import img9 from '../../images/a.jpg';
import img3 from '../../images/ct.jpg';
import img4 from "../../images/dr.jpg";
import img5 from "../../images/th.jpg";
import img6 from "../../images/melbourn.jpg";
import img2 from '../../images/vision.svg';
import little from '../../images/littleUs.svg';
import version from '../../images/Icon.svg';
import { hashHistory } from "react-router";

import './Home.css';
class Home extends Component {
    state = {  }
    navigateToService = () => {
      hashHistory.push("/service");
    };
    render() { 
        return ( 
            <div className="home-container">
                <Container fluid className="hero-container">
                    <Row>
                        <Col xs="1" md="1"></Col>
                        <Col xs="11" md="5">
                                <Row>
                                <h5 className="hero-heading">Level up your business with best IT Services</h5>
                                </Row>
                                <Row>
                                <p className="hero-content">Golden Future Technologies is a recognized and trusted partner for offering complete IT consulting, staffing and migration solutions to augment your business one level up and achieve sustainable digital business success. </p>
                                </Row>
                                <Row>
                                <div className="hero-btn" onClick={this.navigateToService}>
                    Our Services
                </div>
                                </Row>
                        </Col>
                        <Col xs="11" md="4">
                        <img src={img1} alt="hero-img" className="hero-img" />
                        </Col>
                        <Col xs="1" md="1"></Col>
                    </Row>
                </Container>
                <div className="vision-cont">
                <div className="vision-sec">
                <p className="vision-title">Our Vision</p>
                <p className="vision-sub-titile">"Let us help you reach your goals as a company."</p>
                </div>
            </div>
            <div className="vision-body">
                <h5>A little about us</h5>
            <Row>
                <Col></Col>
                <Col xs="12" lg="5" className="vision-left-cont"> 
                <img src={little} alt="version" className="vision-icon" />
                        <p className="sub-heading">What do we do?</p>
                        <br></br>
                        <p className="vision-section">
                        Interweaving our high-end IT solutions into your business and delivering a breath of services, we assist you by streamlining your operations and bolster productivity.
                        </p>
                        <br></br>
                        <p className="vision-section">
                        As a highly skilled and passionate team with a collective experience of more than two decades in the industry, our scalable, resilient and responsive offerings ensure rapid growth and high value for our clients.
                        </p>
                </Col>
                <Col xs="12" lg="5">
                <img src={img2} alt="vision illustration" width="100%"/>
                </Col>
                <Col></Col>
            </Row>
            </div>
            <div className="vision-end">
           <Row> <img src={version} alt="version one" width="50px"/></Row>
                    <p className="vision-sub-heading">
                        What do we provide?
                    </p>
                    <p className="vision-end-content">
                    As our clients expanded globally, we recognized the need to offer ongoing advisory services to businesses in all aspects of immigration and visa related matters. Fast forward today, we serve as a trust worthy point of contact offering assistance with significant resources to relevant experience across borders – where and when our clients need us.
                    </p>
                    <br></br>
                    <p className="vision-end-content-sec">
                    Closely aligned with all your goals, we develop a design concept not just to meet your expectations, but also give you an edge over competitors. Be it solving unique challenges or capitalizing on new opportunities, Golden Future Technologies defines, orchestrates and addresses all your IT and Migration requirements, making you ready for today and tomorrow.
                    </p>
            </div>
            <div className="location-sub-sec">
                    <h6 className="location-head-sec">
                        Locations
                    </h6>
                    <div className="location-cards">
                    <Card>
                <div>
              <Card.Img className="loc-img" variant="top" src={img4}/>
              </div>
              <Card.Body>
                <Card.Title>Adelaide</Card.Title>
                <Card.Text>
                  <span>South Astralia</span>
                </Card.Text>
              </Card.Body>
            </Card>
                    <Card>
                <div>
              <Card.Img className="loc-img" variant="top" src={img8}/>
              </div>
              <Card.Body>
                <Card.Title>Coleambally</Card.Title>
                <Card.Text>
                  <span>NSW</span>
                </Card.Text>
              </Card.Body>
            </Card>
            <Card>
                <div>
              <Card.Img className="loc-img" variant="top" src={img9}/>
              </div>
              <Card.Body>
                <Card.Title>Darwin</Card.Title>
                <Card.Text>
                  <span>Northern territory</span>
                </Card.Text>
              </Card.Body>
            </Card>
            <Card>
                <div>
              <Card.Img variant="top" className="loc-img" src={img3}/>
              </div>
              <Card.Body>
                <Card.Title>Hyderabad</Card.Title>
                <Card.Text>
                  <span>India</span>
                </Card.Text>
              </Card.Body>
            </Card>
            </div></div>
            <div className="end-location-list">
            <Card>
                <div>
              <Card.Img variant="top" className="loc-img" src={img5}/>
              </div>
              <Card.Body>
                <Card.Title>Melbourne</Card.Title>
                <Card.Text>
                  <span>Victoria</span>
                </Card.Text>
              </Card.Body>
            </Card>
            <Card>
                <div>
              <Card.Img variant="top" className="loc-img" src={img4}/>
              </div>
              <Card.Body>
                <Card.Title>Sydney</Card.Title>
                <Card.Text>
                  <span>NSW</span>
                </Card.Text>
              </Card.Body>
            </Card>
            <Card>
                <div>
              <Card.Img variant="top" className="loc-img" src={img6}/>
              </div>
              <Card.Body>
                <Card.Title>Texas</Card.Title>
                <Card.Text>
                  <span>USA</span>
                </Card.Text>
              </Card.Body>
            </Card>
            </div>
            </div>
         );
    }
}
 
export default Home;