import React, { Component } from 'react';
import {Col, Row, Container} from 'react-bootstrap';
import img from '../../images/GFTFull.png';
import socialIcon from '../../images/facebook.svg';
import sIcon from '../../images/instagram.svg';
import socialIcon1 from '../../images/twitter.svg';
import { hashHistory } from "react-router";
import './Footer.css';
class Footer extends Component {
   constructor(props){
       super(props)
       this.state={}
       this.navigateToHome=this.navigateToHome.bind(this);
       this.navigateToContact=this.navigateToContact.bind(this);
       this.navigateToServices=this.navigateToServices.bind(this);
   }
   navigateToHome = () => {
    hashHistory.push("/home");
    // this.props.action(this.props.location.pathname);
  };

  navigateToServices = () => {
    hashHistory.push("/service");
    // this.props.action(this.props.location.pathname);
  };

  navigateToContact = () => {
    hashHistory.push("/contact");
    // this.props.action(this.props.location.pathname);

  };
    render() { 
        return ( 
            <Container fluid className="footer-container">
                <Row>
                    <Col></Col>
                    <Col xs="12" md="4">
                        <img src={img} alt="gft logo" width="200px"/>
                        <p>Golden Future Technologies is a recognized and trusted partner for offering complete IT consulting, staffing and migration solutions to augment your business one level up and achieve sustainable digital business success.</p>
                    </Col>
                    <Col xs="6" md="3">
                    <ul className="footer-links">
                        <li className="footer-link-title"><h6>Pages</h6></li>
                        <li className="footer-link-item" onClick={this.navigateToHome}>Home</li>
                        <li className="footer-link-item" onClick={this.navigateToServices}>Services</li>
                        <li className="footer-link-item" onClick={this.navigateToContact}>Contact Us</li>
                    </ul>
                    </Col>
                    <Col xs="6" md="3" className="footer-social-links">
                        <h6>Social</h6>
                        <div className="fotter-icons">
                <img src={socialIcon} alt="facebook" width="17px"/>&nbsp;&nbsp;&nbsp;
                <img src={sIcon} alt="Instagram" width="17px" className="social-icon" />&nbsp;&nbsp;&nbsp;
                <img src={socialIcon1} alt="Twitter" width="17px" className="social-icon" />
                </div>
                    </Col>
                    <Col></Col>
                </Row>
                <hr></hr>
                <Row>
                    <Col></Col>
                    <Col lg="7">
                <p className="footer-copyright-sec">
                    Copyright @ 2020 Golden Future Technologies
                </p>
                </Col>
                <Col lg="4">
                <p className="footer-location">
                    Laverton Victoria 3028, Australia
                </p>
                </Col>
                </Row>
            </Container>
         );
    }
}
 
export default Footer;