import React, { Component } from 'react';
import { Row, Col, Card } from "react-bootstrap";
import techIcon from '../../../images/staff-icon.svg';
import techIll from '../../../images/tech-illustration.svg';
import './Technologies.css';
class Technologies extends Component {
    state = {  }
    render() { 
        return ( 
            <div className="tech-cont">
                 <div className="itStaffing-Sec">
            <div className="itStaff-head">
            <img src={techIcon} alt="IT Staffing" />
            <h3>Technology Focus</h3>
            <h6>Amplify your Organization’s Capabilities.</h6>
            </div>
            </div>
            <Row>
              <Col md="1" lg="2"></Col>
              <Col xs="12" md="6" lg="4">
              <img src={techIll} className="ill-img" alt="service illustration" />
              </Col>
              <Col xs="12" md="5" lg="5" className="tech-cont-dec">
                <p className="tech-content">Bringing together industry experience and technical skill, we help companies transform digitally and assist them to make informed decisions, respond to change and prepare for the future. We create a roadmap to configure, deploy and implement the right technology, software or application to boost investment in technology and achieve improved business results.</p>
              </Col>
              <Col></Col>
            </Row>
            <h3 className="tech-sub-head">How we Help</h3>
            <Row>
              <Col></Col>
              <Col>
                <Card className="tech-card">
                  <Card.Title>Web Technologies</Card.Title>
                  <Card.Body>
                  Offering a comprehensive array of services, we address client needs for analytics and business information, application and web development, infrastructure operations and help you focus on long-term planning with the latest technologies.
                  </Card.Body>
                </Card>
              </Col>
              <Col>
                <Card className="tech-card">
                  <Card.Title>
                  Cloud
                  </Card.Title>
                  <Card.Body>
                  Operate securely and cost-efficiently by migrating your business to the cloud. We enable you to move your entire IT infrastructure to a cloud platform, to help you achieve a level of scalability, security and mobility you’ve never experienced before.
                  </Card.Body>
                </Card>
              </Col>
              <Col>
              <Card className="tech-card">
                  <Card.Title>
                  Data Warehouse
                  </Card.Title>
                  <Card.Body>
                  As your organization grows, your data will continue to grow exponentially. We help you manage more data, as well as utilize the information in more ways with our data center and network infrastructure. Eliminate the cost and complexity of in-house infrastructure by deploying data inside one of Data Center.
                  </Card.Body>
                </Card>  
              </Col>
              <Col>
              <Card className="tech-card">
                  <Card.Title>
                  Digital Transformation
                  </Card.Title>
                  <Card.Body>
                  Create an enriching experience for your customers by transforming your business and getting ready for your digital journey. From automation to business insights, we can help you improve efficiency, enhance customer experiences and drive growth. Digitization can help you optimize your business performance and enable you to become scalable and quicker.
                  </Card.Body>
                </Card>
              </Col>
              <Col></Col>
            </Row>
            </div>
         );
    }
}
 
export default Technologies;