import React from "react";
import { hashHistory } from "react-router";
import "./App.css";
import {Row, Col} from 'react-bootstrap';
import img from './images/GFTFull.png';
import Footer from './components/Footer/Footer';
import menu from './images/24px.svg';
import Sidebar from "react-sidebar";
// import { withRouter } from 'react-router-dom'
import { Tooltip } from "react-bootstrap";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      isScroller: false,
      prop: "",
      showChatBot: false,
      mobileView: false,
      sidebarOpen: false,
      highlight:"home"
    };
    this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);

    this.validateMobileView = this.validateMobileView.bind(this);
    this.changeNaveVal=this.changeNaveVal.bind(this);
    window.addEventListener("resize", this.validateMobileView);


    // const location = useLocation();
  }

  // renderTooltip(props) {
  //   return (
  //     <Tooltip id="button-tooltip" placement="top" {...props}>
  //       Click here for digital help
  //     </Tooltip>
  //   );
  // }
  changeNaveVal(val){
    console.log(val)
    this.setState({highlight:val});
  }
  componentDidMount() {
    this.validateMobileView();

  }
  

  onSetSidebarOpen(open) {
    this.setState({ sidebarOpen: open });
  }
  validateMobileView = () => {
    console.log()
    setTimeout(()=>{
      if(this.props.location.pathname){
        const val=this.props.location.pathname.toString().length>1?this.props.location.pathname.toString().replace(/[/]/g, ''):'home'
        this.setState({highlight:val})
      //  this.changeNaveVal(val)
      }
      hashHistory.listen((ev) => {
      
        if(ev.pathname.toString().replace(/[/]/g, '')){
          const val=ev.pathname.toString().replace(/[/]/g, '')?ev.pathname.toString().replace(/[/]/g, ''):'home'
          this.setState({highlight:val})
        //  this.changeNaveVal(val)
        };
      });
    })
    if (window.innerWidth <= 800) {
      this.setState({ mobileView: true });
    } else {
      this.setState({ mobileView: false });
    }
  };
  // componentDidMount() {
  //   this.onPathChange('home');
  // }
  onPathChange(val) {
    let path=val.toString().length >1?val.toString().replace(/[/]/g, ''):'home';
    setTimeout(()=>{
      this.setState({
        highlight:path
      })
    },10);
    this.validateMobileView();
  }
  navigateToHome = () => {

    hashHistory.push("/home");
    this.onSetSidebarOpen();
    setTimeout(()=>{
    this.setState({
      highlight:this.props.location.pathname.toString().replace(/[/]/g, '')
    })
  },10);
    this.onSetSidebarOpen();
  };

  navigateToService = () => {
    console.log(this.props.location.pathname.toString().replace(/[/]/g, ''))

    hashHistory.push("/service");
    this.onSetSidebarOpen();
    setTimeout(()=>{
    this.setState({
      highlight:this.props.location.pathname.toString().replace(/[/]/g, '')
    })
  },10);
    // this.onSetSidebarOpen();
  };

  navigateToContact = () => {
    hashHistory.push("/contact");
    this.onSetSidebarOpen();
    setTimeout(()=>{
    this.setState({
      highlight:this.props.location.pathname.toString().replace(/[/]/g, '')
    })
  },10);
    // this.onSetSidebarOpen();
  };

  chatBotOpt = () => {
    this.setState({ showChatBot: !this.state.showChatBot });
  };

  render() {
    return (
      <div className="main-container">
       {!this.state.mobileView && <Row className="header-container ">
          <Col xs="2" lg="2" className="logo">
              <img src={img} alt="logo-img" height="104px" />
          </Col>
        <Row className="nav col-10">
            <Col lg="8"></Col>
              <Col lg="1" className={"nav-item " + (this.state.highlight === 'home'?'navActive':'')} onClick={this.navigateToHome}>
                      Home
              </Col>
              <Col lg="1" className={"nav-item " + (this.state.highlight === 'service'?'navActive':'')} onClick={this.navigateToService}>
                  Services
              </Col>
              <Col lg="2" className={"nav-item " + (this.state.highlight === 'contact'?'navActive':'')} onClick={this.navigateToContact}>
                  Contact Us
              </Col>
          </Row>
          </Row>}
          {this.state.mobileView &&   <Sidebar
                      sidebar={
                        <ul>
                          <li className="close-btn">X</li>
                          <li
                            className={"sidebar-item " + (this.state.highlight === 'home'?'sideActive':'')}
                            onClick={this.navigateToHome}
                          >
                            HOME
                          </li>
                          <li
                            className={"sidebar-item " + (this.state.highlight === 'service'?'sideActive':'')}
                            onClick={this.navigateToService}
                          >
                            SERVICES
                          </li>
                          <li
                            className={ "sidebar-item " + (this.state.highlight === 'contact'?'sideActive':'')}
                            onClick={this.navigateToContact}
                          >
                            CONTACT US
                          </li>
                        </ul>
                      }
                      open={this.state.sidebarOpen}
                      onSetOpen={this.onSetSidebarOpen}
                      styles={{
                        sidebar: {
                          background: "#fff",
                          width: "100%",
                          height: "196%",
                        },
                      }}
                    >
                      <img
                        src={menu}
                        className="menu-item"
                        alt="menu"
                        onClick={this.onSetSidebarOpen}
                        width="40px"
                      />
                    </Sidebar>}
                    {this.state.mobileView && <div className="mobile-view-logo"> <img src={img} alt="logo-img" height="64px" /></div> }
      {this.props.children}
      {/* <div className="chat-sec">
            <OverlayTrigger placement="top" overlay={this.renderTooltip}>
                <img
                  src={chatbot}
                  alt="Click here for digital help"
                  width="65px"
                  onClick={this.chatBotOpt}
                />
            </OverlayTrigger>
            {this.state.showChatBot && <ChatComp />}
          </div> */}
      <Footer />
</div>
    );
  }
}

export default App;
