import React, { Component } from 'react';
import './ContactUs.css';
import img from '../../images/contactIll.svg';
import { Modal } from 'react-bootstrap';
import 'react-phone-number-input/style.css'
import PhoneInput from 'react-phone-number-input';
import { isValidPhoneNumber } from 'react-phone-number-input'


class ContactUs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
        emailId:'',
        phoneNumber:undefined,
        countries:[],
    msg:'',
    disableBtn:false,
    formErrors: {
      'nameErr':'',
      'msgErr':'',
      'emailIdErr':'',
      'phoneNumber':''
    },
    show:false,
    mobileView:false
  
  };
  this.handleClose=this.handleClose.bind(this);
  this.handleOpen=this.handleOpen.bind(this);
  this.initialState = this.state;
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleMSGChange = this.handleMSGChange.bind(this);
        this.handlePSWChange = this.handlePSWChange.bind(this);
        this.handlePhoneChange = this.handlePhoneChange.bind(this);
        this.handleFormValidation=this.handleFormValidation.bind(this);
        this.handleMsgField=this.handleMsgField.bind(this);
        this.handleNameField=this.handleNameField.bind(this);
        this.handlePSWField=this.handlePSWField.bind(this);
        this.handlePhoneField=this.handlePhoneField.bind(this);
        this.validateMobileView=this.validateMobileView.bind(this);
        window.addEventListener('resize', this.validateMobileView);
      }
        validateMobileView = () =>{
          if(window.innerWidth<=800){
          this.setState({mobileView:true});
          }else{
            this.setState({mobileView:false});
          }
          console.log(this.state.mobileView,window.innerWidth)
        }
        componentDidMount() {
          this.validateMobileView()
      }

      
    
      handleFormValidation() {    
        const { name, emailId, phoneNumber, msg } = this.state;    
        let formErrors = {};    
        let formIsValid = true;    
    
        //Student name     
        if (!name) {    
            formIsValid = false;    
            formErrors["nameErr"] = "Name is required.";    
        }    
    
        //Email    
        if (!emailId) {    
            formIsValid = false;    
            formErrors["emailIdErr"] = "Email id is required.";    
        }    
        else if (!(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(emailId))) {    
    
            formIsValid = false;    
            formErrors["emailIdErr"] = "Invalid email id.";    
        }    
        //Phone number    
        if (!phoneNumber) {    
            formIsValid = false;    
            formErrors["phoneNumberErr"] = "Phone number is required.";    
        }    
        else {    
            var mobPattern = /^\+(?:[0-9] ?){6,14}[0-9]$/;    
            if (!mobPattern.test(phoneNumber)) {    
                formIsValid = false;    
                formErrors["phoneNumberErr"] = "Invalid phone number.";    
            }    
        }    
      
        // Message
        if(!msg) {
          formIsValid = false;
          formErrors["msgErr"] = "Message is required.";
        }
    
        this.setState({ formErrors: formErrors });    
        return formIsValid;    
    }    

    handleClose() {
      this.setState({show:false,disableBtn:true,name:'',emailId:'',phoneNumber:'',msg:''});
    }
    handleOpen() {
      this.setState({show:true});
    }
    handleNameField() {
      const formErrors = {}
      if (!this.state.name) {     
        formErrors["nameErr"] = "Name is required.";    
    }  
    if(this.state.formErrors){
      this.setState({formErrors:{
      ...this.state.formErrors,
    formErrors}})
    }
    else{
    this.setState({formErrors:
      formErrors})
    }
    }
    phoneNumberChange(newValue){
      this.setState({phoneNumber:newValue})
    }
    componentDidMount() {
      // const [phone, setValue] = useState();
    }
    handlePSWField() {
      const formErrors = {}
      if (!this.state.emailId) {    
       
        formErrors["emailIdErr"] = "Email id is required.";    
    }    
    else if (!(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.state.emailId))) {      
        formErrors["emailIdErr"] = "Invalid email id.";    
    }  
    this.setState({formErrors:
      formErrors})
    }
    handlePhoneField() {
      const formErrors = {}
      if (!this.state.phoneNumber) {     
        formErrors["phoneNumberErr"] = "Phone number is required.";    
    }    
    else {    
        var mobPattern = /^\+(?:[0-9] ?){6,14}[0-9]$/;    
        if (!mobPattern.test(this.state.phoneNumber)) {    
            formErrors["phoneNumberErr"] = "Invalid phone number.";    
        }    
    } 
    this.setState({formErrors:
      formErrors})
    }

    handleMsgField(){
      const formErrors = {}
      if(!this.state.msg) {
        formErrors["msgErr"] = "Message is required.";
      }
      this.setState({formErrors:
        formErrors})
    }

      handleChange(event) {
        this.setState({name: event.target.value});
      }
    
      handleSubmit(event) {
        // alert('A name was submitted: ' + this.state.name);
        event.preventDefault();
         if(this.handleFormValidation()){
          this.handleOpen();
        fetch(`/send-email?senderName=${this.state.name}&email=${this.state.emailId}&phoneNo=${this.state.phoneNumber}&message=${this.state.msg}`)
        .then(res=> {
          console.log(res)
          this.handleOpen();
          
        })
        .catch(err=> console.log(err))
        }
      }

      handlePSWChange(event) {
        this.setState({emailId: event.target.value});
      }
      handlePhoneChange(event) {
        this.setState({phoneNumber:event.target.value})
      }
      handleMSGChange(event) {
        this.setState({msg: event.target.value});
      }


      render() {
        const { countries } = this.state;
	let countriesList = countries.length > 0
		&& countries.map((item, i) => {
		return (
			<option key={i} value={item.number}>{item.number}</option>
		)
	}, this); 
        const { nameErr, emailIdErr, phoneNumberErr, msgErr } = this.state.formErrors; 
        const popupClass=!this.props.fromPopup?(!this.state.mobileView?"form-fields-and-btn":"for-mobile-form-field"):(this.state.mobileView?"mobile-popup-fields":"form-fields-popup")
        return ( 
          <div>
          <div className="contact-container">
             {!this.props.fromPopup && <div className="hero-sec">
                  <div className="contact-header-sec">
                      <img src={img} alt="contact illution" />
                      <h3 className="contact-head">
                          Contact Us
                      </h3>
                      <p className="contact-sub">
                      We are here to help and answer any questions you may have. We are looking forward to hearing from you.
                      </p>
                  </div>
              </div>}
              <div className={!this.props.fromPopup?"contact-body":"contact-popup-body"}>
                  <form onSubmit={this.handleSubmit}>
                  <label className="form-field-label">
            Name:*
            </label>
            <input className={!this.props.fromPopup?"form-field-input":"popup-form-field"} type="text" value={this.state.name} onChange={this.handleChange} onBlur = {this.handleNameField}/>
            {nameErr &&    
                              <div style={{ color: "red", paddingLeft: 20 }}>{nameErr}</div>    
                          }  
            <label className="form-field-label">
            Email:*
            </label>
            <input className={!this.props.fromPopup?"form-field-input":"popup-form-field"} type="text" value={this.state.emailId} onChange={this.handlePSWChange} onBlur = {this.handlePSWField}/>
            {emailIdErr &&    
                              <div style={{ color: "red", paddingLeft: 20 }}>{emailIdErr}</div>
            }
            <label className="form-field-label">
            Phone Number:*
            </label>
            <PhoneInput
            className={!this.props.fromPopup?"form-field-input":"popup-form-field"}
      value={this.state.phoneNumber}
      onChange={phoneNumber=>this.setState({phoneNumber})} onBlur={this.handlePhoneField}/>
        {isValidPhoneNumber(this.state.phoneNumber)}
            {/* <input className={!this.props.fromPopup?"phone-input-field form-field-input":"popup-form-field"} type="text" value={this.state.phoneNumber} onChange={this.handlePhoneChange} onBlur={this.handlePhoneField}/> */}
            {phoneNumberErr &&    
                              <div style={{ color: "red", paddingLeft: 20 }}>{phoneNumberErr}</div> 
            }
              <label className="form-field-label">
                  Query or Comments
              </label>
              <textarea className={!this.props.fromPopup?"form-field-desc":"popup-form-field"} value={this.state.msg} onChange={this.handleMSGChange} onBlur={this.handleMsgField}/>
              {msgErr &&    
                              <div style={{ color: "red", paddingLeft: 20 }}>{msgErr}</div> 
            }
                  <button className={"form-btn " + (this.state.disableBtn?"disable-btn":"")}>Submit</button>
                  </form>
              </div>
          </div>
          <Modal size="lg" show={this.state.show} onHide={this.handleClose}>
        <Modal.Header closeButton>
        your query shared successfully.
        </Modal.Header>
      </Modal>
          </div>
       );
      }
}
 
export default ContactUs;